import React from 'react'
import { Link } from 'react-router-dom'
import styles from './CartBlock.module.scss'
import CartItem from '../CartItem'

import { useSelector } from 'react-redux'
import { selectCart } from '../../redux/cart/selectors'

import arrow from '../../assets/img/svg/arrow.svg'
import cartNone from '../../assets/img/svg/cart-none.svg'
// import content from '../../assets/img/content/content1.jpg'
// import pl from '../../assets/img/svg/pl.svg'

export const CartBlock: React.FC = () => {
  const { totalPrice, items, delivery } = useSelector(selectCart)

  const totalCount = items.reduce(
    (sum: number, item: any) => sum + item.count,
    0,
  )

  return (
    <section className={styles.cart}>
      <div className={styles.container}>
        <Link className={styles.btn__back} to="/">
          <img className={styles.btn__back__img} src={arrow} alt="arrow" />
          <span>  </span>
        </Link>
        <div className={styles.title__wrapper}>
          <div className={styles.title__border}></div>
          <h3 className={styles.title__name}></h3>
          <div className={styles.title__info}>
            ( 
            <div className={styles.title__info__count}>{totalCount}</div>
            )
          </div>
        </div>

        {!totalCount ? (
          <div className={styles.cart__none}>
            <img className={styles.cart__none__img} src={cartNone} alt="cart" />
            <h3 className={styles.cart__none__title}> </h3>
            <Link className={styles.cart__none__btn} to="/">
               
            </Link>
          </div>
        ) : (
          <>
            <div className={styles.cart__wrapper}>
              {items.map((item: any) => (
                <CartItem key={item.id} {...item} />
              ))}
            </div>
            {/* <h2 className={styles.title__add}>  </h2>
            <div className={styles.rec}>
              <div className={styles.rec__box}>
                <img
                  className={styles.rec__box__img}
                  src={content}
                  alt="content1"
                />
                <h4 className={styles.rec__box__title}> </h4>
                <div className={styles.rec__box__btn}>
                  <div className={styles.rec__btn__name}></div>
                  <button className={styles.rec__btn__link}>
                    <img src={pl} alt="pl" />
                  </button>
                </div>
                <span className={styles.rec__box__price}>1640 </span>
              </div>
              <div className={styles.rec__box__border}></div>
              <div className={styles.rec__box}>
                <img
                  className={styles.rec__box__img}
                  src={content}
                  alt="content1"
                />
                <h4 className={styles.rec__box__title}> </h4>
                <div className={styles.rec__box__btn}>
                  <div className={styles.rec__btn__name}></div>
                  <button className={styles.rec__btn__link}>
                    <img src={pl} alt="pl" />
                  </button>
                </div>
                <span className={styles.rec__box__price}>1640 </span>
              </div>
              <div className={styles.rec__box__border}></div>
              <div className={styles.rec__box}>
                <img
                  className={styles.rec__box__img}
                  src={content}
                  alt="content1"
                />
                <h4 className={styles.rec__box__title}> </h4>
                <div className={styles.rec__box__btn}>
                  <div className={styles.rec__btn__name}></div>
                  <button className={styles.rec__btn__link}>
                    <img src={pl} alt="pl" />
                  </button>
                </div>
                <span className={styles.rec__box__price}>1640 </span>
              </div>
              <div className={styles.rec__box__border}></div>
              <div className={styles.rec__box}>
                <img
                  className={styles.rec__box__img}
                  src={content}
                  alt="content1"
                />
                <h4 className={styles.rec__box__title}> </h4>
                <div className={styles.rec__box__btn}>
                  <div className={styles.rec__btn__name}></div>
                  <button className={styles.rec__btn__link}>
                    <img src={pl} alt="pl" />
                  </button>
                </div>
                <span className={styles.rec__box__price}>1640 </span>
              </div>
            </div> */}
            {/* <div className={styles.border}></div> */}
            <div className={styles.offer}>
              <div className={styles.offer__left}>
                <div className={styles.offer__left__first}>
                  <div className={styles.offer__first__name}>:</div>
                  <div className={styles.offer__first__price}>
                    {totalPrice} 
                  </div>
                </div>

                {delivery >= 0 ? (
                  <div className={styles.offer__left__second__delivery}>
                    <div className={styles.offer__second__name__delivery}>
                       !
                    </div>
                  </div>
                ) : (
                  <>
                    <div className={styles.offer__left__second}>
                      <div className={styles.offer__second__name}>
                            :
                      </div>
                      <div className={styles.offer__second__price}>
                        {delivery >= 0 ? 0 : Math.abs(delivery)} 
                      </div>
                    </div>
                    <div className={styles.offer__left__therd}>
                         1500 
                    </div>
                  </>
                )}
              </div>
              <Link className={styles.offer__right} to="/cart/order">
                 
              </Link>
            </div>
          </>
        )}
      </div>
    </section>
  )
}
